import { isEqualValueDefault } from "@ixfx/core";
import { initStream } from "../init-stream.js";
import { compareArrays } from "@ixfx/core/records";
import { remove as ArraysRemove, insertAt as ArraysInsertAt } from '@ixfx/arrays';
/**
 * Wraps an array object.
 *
 * It returns an reactive along with some array-ish functions to manipulating it.
 * @param initialValue
 * @param options
 * @returns
 */
export function arrayObject(initialValue = [], options = {}) {
    const eq = options.eq ?? isEqualValueDefault;
    const setEvent = initStream();
    //const diffEvent = initStream<Array<Immutable.Change<any>>>();
    const arrayEvent = initStream();
    let value = initialValue;
    let disposed = false;
    const set = (replacement) => {
        const diff = compareArrays(value, replacement, eq);
        //console.log(`Rx.fromArray.set diff`, diff);
        //if (diff.length === 0) return;
        //diffEvent.set(diff);
        value = replacement;
        setEvent.set([...replacement]);
    };
    const setAt = (index, v) => {
        value[index] = v;
        setEvent.set([...value]);
    };
    const push = (v) => {
        value = [...value, v];
        setEvent.set([...value]);
        const cr = [`add`, value.length - 1, v];
        arrayEvent.set([cr]);
    };
    const deleteAt = (index) => {
        const valueChanged = ArraysRemove(value, index);
        if (valueChanged.length === value.length)
            return; // no change
        const diff = compareArrays(value, valueChanged, eq);
        //console.log(diff.summary);
        value = valueChanged;
        setEvent.set([...value]);
        arrayEvent.set(diff.summary);
    };
    const deleteWhere = (filter) => {
        const valueChanged = value.filter(v => !filter(v));
        const count = value.length - valueChanged.length;
        const diff = compareArrays(value, valueChanged, eq);
        value = valueChanged;
        setEvent.set([...value]);
        arrayEvent.set(diff.summary);
        return count;
    };
    const insertAt = (index, v) => {
        const valueChanged = ArraysInsertAt(value, index, v);
        const diff = compareArrays(value, valueChanged, eq);
        value = valueChanged;
        setEvent.set([...value]);
        arrayEvent.set(diff.summary);
    };
    // const update = (toMerge: Partial<V>) => {
    //   // eslint-disable-next-line unicorn/prefer-ternary
    //   if (value === undefined) {
    //     value = toMerge as V;
    //   } else {
    //     const diff = Immutable.compareData(toMerge, value);
    //     // console.log(`Rx.fromObject.update value: ${ JSON.stringify(value) }`);
    //     // console.log(`Rx.fromObject.update  diff: ${ JSON.stringify(diff) }`);
    //     if (diff.length === 0) return; // No changes
    //     value = {
    //       ...value,
    //       ...toMerge
    //     }
    //     diffEvent.set(diff);
    //   }
    //   setEvent.set(value);
    // }
    const dispose = (reason) => {
        if (disposed)
            return;
        //diffEvent.dispose(reason);
        setEvent.dispose(reason);
        disposed = true;
    };
    const r = {
        dispose,
        isDisposed() {
            return disposed;
        },
        last: () => value,
        on: setEvent.on,
        onArray: arrayEvent.on,
        onValue: setEvent.onValue,
        setAt,
        push,
        deleteAt,
        deleteWhere,
        insertAt,
        /**
         * Set the whole object
         */
        set
    };
    return r;
}
