/**
 * Initialises a reactive that pipes values to listeners directly.
 * @returns
 */
export function manual(options = {}) {
    const events = initStream(options);
    return {
        dispose: events.dispose,
        isDisposed: events.isDisposed,
        set(value) {
            events.set(value);
        },
        on: events.on,
        onValue: events.onValue
    };
}
